tva steena na ovim takmienjima, stalno prenosim na moje prijatelje i moj tim KPS GROM Poarevac. U svojoj pucakoj karijeri uestvovao sam na raznim evropskim takmienjima, GECO open Maarska, Extrem open, eka Republika, Duklej Crna Gora, Dellta Open Srbija itd. Paralelno sa gore navedenim aktivnostima, uspeno vodim etvorolanu ekipu, lanova KPS Grom, koji posle temeljne i kompleksne obuke poinje da nastupa u IPSC ligi Srbije.\n            "])])])])
